<template>
  <div class="the-wallet-ledger-signup">
    <div class="the-wallet-ledger-signup__container">
      <form
        class="the-wallet-ledger-signup__form"
        @submit.prevent
      >
        <div class="the-wallet-ledger-signup__inputbox no-label">
          <e-select
            v-model="pathType"
            :icon="iconSecretLock"
            :options="pathTypeOptions"
          />
        </div>
        <div v-if="pathType === 'custom'" class="the-wallet-ledger-signup__inputbox">
          <e-text-input
            v-model="customDerivationPath"
            :leftOuterIcon="iconWalletAddress"
            label="Input a path m/44'/60'/"
          />

          <e-button
            :disabled="!isValidCustomDerivationPath"
            fill="blue-blue"
            type="squared"
            size="ss"
            padding="xss"
            class="the-wallet-ledger-signup__button-path-load"
            @click="updateLedgerAccounts"
          >
            Load accounts
          </e-button>
        </div>
        <div class="the-wallet-ledger-signup__inputbox">
          <e-select
            v-model="selectedLedgerAccountIndex"
            label="Select wallet"
            :icon="iconWalletAddress"
            :options="walletOptions"
          />
        </div>

        <div class="the-wallet-ledger-signup__sbmt">
          <e-button
            type="default"
            fill="green"
            width="s"
            :icon="iconWalletImport"
            :disabled="isImportDisabled"
            @click="importWallet"
            data-test="importWallet"
          >
            Import wallet
          </e-button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { checkLedgerSupport, getLedgerAccounts } from '@/api/ethereum';
import getDerivationPathFromLast3Levels from '@/util/crypto/getDerivationPathFromLast3Levels';

import ETextInput from '@/components/ETextInput';
import EButton from '@/components/EButton';
import ESelect from '@/components/ESelect';
import EIcon from '@/components/EIcon';

import iconWalletImport from '@/icons/e-icon_wallet_import.svg';
import iconWalletAddress from '@/icons/e-icon_wallet-adress.svg';
import iconSecretLock from '@/icons/e-icon_secret-lock.svg';

export default {
  name: 'the-wallet-ledger-signup',

  data() {
    return {
      pathType: 'live',
      customDerivationPath: '',
      ledgerAccounts: [],
      selectedLedgerAccountIndex: -1,
    };
  },

  watch: {
    pathType() {
      if (this.pathType === 'live') {
        this.updateLedgerAccounts();
      } else if (this.pathType === 'custom') {
        this.customDerivationPath = '';
        this.ledgerAccounts = [];
        this.selectedLedgerAccountIndex = -1;
      }
    },
  },

  computed: {
    isValidCustomDerivationPath() {
      return !!this.customDerivationPath;
    },

    isImportDisabled() {
      return this.$v.$invalid || this.selectedLedgerAccountIndex === -1;
    },

    walletOptions() {
      return this.ledgerAccounts.map((account, i) => ({
        label: account.address,
        value: i,
      }));
    },
  },

  validations: {
  },

  created() {
    Object.assign(this, {
      pathTypeOptions: [
        {
          label: 'Ledger live',
          value: 'live',
        },
        {
          label: 'Custom',
          value: 'custom',
        },
      ],
      iconWalletImport,
      iconWalletAddress,
      iconSecretLock,
    });
    this.lastUpdateAccountsRequestId = -1;
  },

  mounted() {
    this.updateLedgerAccounts();
  },

  methods: {
    async updateLedgerAccounts() {
      const derivationPath = this.pathType === 'live'
        ? "m/44'/60'/0'/0/0"
        : getDerivationPathFromLast3Levels(this.customDerivationPath);
      this.ledgerAccounts = [];
      this.selectedLedgerAccountIndex = -1;
      this.lastUpdateAccountsRequestId += 1;
      const expectedRequestId = this.lastUpdateAccountsRequestId;
      if (await checkLedgerSupport()) {
        if (this.lastUpdateAccountsRequestId !== expectedRequestId) return;
        this.ledgerAccounts = await getLedgerAccounts(derivationPath);
        if (this.lastUpdateAccountsRequestId !== expectedRequestId) return;
        if (this.ledgerAccounts.length === 0) return;
        this.selectedLedgerAccountIndex = 0;
      }
    },

    async importWallet() {
      const wallet = this.ledgerAccounts[this.selectedLedgerAccountIndex];
      this.$store.commit('wallets/SET_LEDGER_WALLETS', [wallet]);
      this.$store.dispatch('wallets/setActiveWallet', wallet);
      this.$ga.event({
        eventCategory: 'login',
        eventAction: 'login_successful',
        eventLabel: 'ledger',
      });
      this.goToTrading();
    },

    goToTrading() {
      this.$router.push({ name: 'futures' });
    },
  },

  components: {
    ETextInput,
    EButton,
    ESelect,
    EIcon,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-ledger-signup {
  &__container {
    margin-top: 30px;
    max-width: 625px;

    @include r(1200) {
      max-width: 439px;
    }
  }

  &__inputbox {
    max-width: 450px;
    position: relative;
    margin-left: -50px;
    margin-bottom: 30px;

    @include r(1200) {
      margin-bottom: 20px;
      margin-left: 0;
    }

    &.no-label {
      .e-select__content {
        display: flex;
        align-items: center;
      }

      .e-select__selected-label {
        top: 0;
      }
    }
  }

  &__button-path-load {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translate(0, -50%) !important;

    &:hover {
      transform: translate(0, -50%) !important;
    }

    &:active {
      transform: translate(0, calc(-50% + 1px)) !important;
    }

    &[disabled] {
      background: #aaa !important;
      box-shadow: none !important;
    }
  }

  .e-select.expanded {
    z-index: 2;
  }

  .e-select,
  .e-text-input {
    .e-icon {
      svg {
        fill: #1fce43;

        .e-icon_wallet-adress_darked {
          fill: #0D8E22;
        }
      }
    }
  }
}
</style>




